// 
<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-button
          type="success"
          icon="search"
          @click="addClick"
        ></el-button>
      </div>
      <el-table
        :data="list"
        border
        style="width: 100%"
        fit
        v-loading="listLoading"
        @row-dblclick="handleDoubleClick"
        highlight-current-row
      >
        <el-table-column
          prop="storename"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="tel"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
        >
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{ scope.row.creatdate | parseTime('{y}-{m}-{d} {h}:{i}') }}</span>
          </template>
        </el-table-column>
        <el-table-column
          fixed="right"
          label=""
        >
          <template slot-scope="scope">
            <el-button
              @click="handleClick(scope.row)"
              type="success"
              size="small"
            ></el-button>
            <!-- <el-button type="text" size="small"></el-button> -->
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          @prev-click='prevClick'
          @next-click='nextClick'
          background
          layout="total, sizes, prev, pager, next, jumper"
          :total='total'
          :page-sizes="[10,15,20,50]"
          :page-size="size"
          :current-page="pageNum"
        >
        </el-pagination>
      </div>
    </div>

    <addstore
      ref="addstore"
      :getList="getList"
    ></addstore>
    <editstore ref="editstore"></editstore>

  </div>
</template>

<script>
import { fetchList } from '@/api/company/store.js'
import addstore from './addstore'
import editstore from './editstore'
var _ = require('lodash')

export default {
  components: {
    addstore,
    editstore
  },
  data () {
    return {
      listQuery: {
        page: 0, // 
        size: 10 // 
      },
      listLoading: true,
      list: [],
      pageNum: 0, // 
      pages: 0, // 
      pageSize: 0, // 
      size: 0, // 
      total: 0 // 
    }
  },
  created () {
    this.getList()
  },
  methods: {
    // 
    addClick () {
      this.$refs.addstore.visible = true
    },
    // 
    handleDoubleClick (row, event) {
      console.log(row)
      this.$refs.editstore.visible = true
      let store = _.cloneDeep(row)
      this.$refs.editstore.store = store
      // this.$refs.editstore.setCity()
    },
    //   
    handleClick (store) {
      this.editDialog = true
      this.store = store
    },
    // 
    getList () {
      this.listLoading = fetchList(this.listQuery)
        .then(response => {
          this.list = response.data.list
          this.pages = response.data.pages
          this.pageSize = response.data.pageSize
          this.total = response.data.total
          this.pageNum = response.data.pageNum
          console.log('')
          console.log(response)
          this.listLoading = false
          // this.total = response.data.data.total
          // setTimeout(() => {
          //   this.listLoading = false
          // }, 1.5 * 1000)
        })
        .catch(error => {
          console.log('' + error)
          this.listLoading = false
        })
    },
    // 
    handleSizeChange (val) {
      this.listQuery.size = val
      this.getList()
      console.log(` ${val} `)
    },
    // 
    handleCurrentChange (val) {
      this.listQuery.page = val
      this.getList()
      console.log(`: ${val}`)
    },
    // 
    prevClick () {
      ++this.listQuery.size
      this.getList()
    }, // 
    nextClick () {
      --this.listQuery.size
      this.getList()
    }
  }
}
</script>

<style scoped>
.handle-box {
  margin-bottom: 10px;
}

.handle-select {
  width: 120px;
}
.handle-input {
  width: 300px;
  display: inline-block;
}
.del-dialog-cnt {
  font-size: 16px;
  text-align: center;
}
.table {
  width: 100%;
  font-size: 14px;
}
.red {
  color: #ff0000;
}
</style>



