// 
<template>
  <el-dialog
    title=""
    :visible.sync="visible"
    width="800px"
    close="close"
  >
    <el-form
      ref="form"
      :model="company"
      label-width="160px"
    >
      <el-row>
        <el-col :span="12">
          <el-form-item label="">
            <el-input
              v-model="company.cname"
              :disabled="true"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-input
              v-model="company.tel"
              :disabled="true"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>

    <el-form
      ref="form"
      :model="store"
      label-width="160px"
    >
      <el-row>
        <el-col :span="12">
          <el-form-item label="">
            <el-input v-model="store.storename"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-input v-model="store.tel"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="">
            <el-cascader
              :options="cityOptions"
              :value="city"
              @change="changeProvince"
              change-on-select
            >
            </el-cascader>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-input v-model="store.address"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="">
            <el-switch v-model="store.memberenter"></el-switch>
          </el-form-item>
        </el-col>

        <el-col :span="12">
          <el-form-item label="">
            <el-switch v-model="store.memberconsume"></el-switch>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-switch v-model="store.membershopteach"></el-switch>
          </el-form-item>
        </el-col>
        <el-col :span="12">

          <el-form-item label="">
            <el-switch v-model="store.intententer"></el-switch>
          </el-form-item>
        </el-col>
        <el-col :span="12">

          <el-form-item label="">
            <el-switch v-model="store.intentvisit"></el-switch>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-switch v-model="store.teachsign"></el-switch>
          </el-form-item>
        </el-col>
        <el-col :span="12">

          <el-form-item label=",">
            <el-switch v-model="store.employentry"></el-switch>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label=",">
            <el-switch v-model="store.memberexpire"></el-switch>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <span
      slot="footer"
      class="dialog-footer"
    >
      <el-button @click="close"> </el-button>
      <el-button
        type="primary"
        @click="add"
      > </el-button>
    </span>
  </el-dialog>

</template>

<script>
import { add } from '@/api/company/store.js'
import cityOptions from '@/common/city_data2017_element.js'
export default {
  data: function () {
    return {
      city: [],
      cityOptions: cityOptions,
      visible: false,
      company: {
        address: '',
        cname: '2',
        companycode: 'SX995719',
        creatdate: null,
        creatid: '',
        remark: 'fasdfasdf',
        rowid: '264035c4-e7ed-11e8-9a51-00ff20a0196f',
        state: true,
        tel: '18892081443',
        updadate: null,
        updaid: ''
      },
      store: {
        address: '',
        city: '',
        companyid: '',
        county: '',
        creatdate: '',
        creatid: '',
        employentry: true,
        intententer: true,
        intentvisit: true,
        memberbirthday: true,
        memberconsume: true,
        memberenter: true,
        memberexpire: true,
        membershopteach: true,
        province: '',
        rowid: '',
        status: true,
        storename: '',
        teachsign: true,
        tel: '',
        updadate: '',
        updaid: ''
      }
    }
  },

  methods: {
    changeProvince (citycity) {
      this.city = citycity
      if (this.city.length === 3) {
        this.store.province = this.city[0]
        this.store.city = this.city[1]
        this.store.county = this.city[2]
      }
      if (this.city.length === 2) {
        this.store.province = this.city[0]
        this.store.city = this.city[1]
      }
      if (this.city.length === 1) {
        this.store.province = this.city[0]
      }
      console.log(this.store)
    },
    close () {
      this.visible = false
    },
    add () {
      this.store.companyid = this.company.rowid
      add(this.store).then(response => {
        if (response.code === 200) {
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
          this.getList()
          this.close()
        }
      })
    }
  },
  props: ['getList']
}
</script>

<style>
</style>



