import request from '@/utils/request'
// 
// 
export function fetchList (query) {
  return request({
    url: '/store',
    method: 'Post',
    params: query
  })
}
// 
export function add (data) {
  return request({
    url: '/store/add',
    method: 'Post',
    data
  })
}
// id
export function detail (id) {
  return request({
    url: '/store/detail',
    method: 'Post',
    id
  })
}
// 
export function update (data) {
  return request({
    url: '/store/update',
    method: 'post',
    data
  })
}
// 
export function Delete (id) {
  return request({
    url: '/store/delete',
    method: 'post',
    id
  })
}



